html, body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-y: hidden;
}
body {
  height: 100%;
  width: 100%;
  background-size: cover;
}

@media only screen and (max-width: 450px) {
	body{
    background-position-y: bottom;
	}	
}

#root {
  height: 100%;
  width: 100%;
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}