#HeaderItem{
	display: flex;
	flex: 1 1 auto;
	background-color: white;
	border-radius: 15px 15px;
	margin: 0px 25px;
	height: 75px;
	justify-content: center;
	align-items: center;
	font-size: 3vmin;
	width: 20vmin;
	text-align: center;
	cursor: pointer;
	text-decoration: none;
}

#HeaderItem > p{
	-webkit-background-clip: text;
}
@media only screen and (max-width: 1024px) {
	#HeaderItem{
		font-size: 2vw;
		width: 13vw;
		height: 10vmin;
		margin-top: 2vh;
	}
}

@media only screen and (max-width: 767px) {
	#HeaderItem{
		font-size: 3vmin;
		height: 10vmin;
		width: 15vw;
		margin-top: 2vh;
	}
}

@media only screen and (max-width: 540px) {
	#HeaderItem{
		font-size: 2.8vmin;
		height: 10vmin;
		width: 25vw;
		margin-top: 2vh;
		margin: 10px 10px;
	}
}
