#header {
  display: flex;
  margin: 35px 35px;
  justify-content: flex-start;
  align-items: center;
  height: min-content;
}

#items{
	display: flex;
	margin-top: 20px;
}


@media only screen and (max-width: 1024px) {
  #logo {
    height: 20vh;
    margin: 0px 0px;
  }
}
@media only screen and (max-height: 600px) {
  #header {
    display: flex;
    flex-direction: row;
    margin: 20px 0px;
    justify-content: center;
    align-items: center;
  }
}

@media only screen and (max-width: 768px) {
  #logo {
    height: 19vh;
    margin: 0px 0px;
  }
}

@media only screen and (max-width: 540px) {
  #logo {
    height: 17vh;
    margin: 0px 0px;
  }
  #header {
    display: flex;
    flex-direction: column;
    margin: 20px 0px;
    justify-content: center;
    align-items: center;
  }
}
/* ipad pro */

@media only screen and (min-width: 1024px) and (max-height: 1366px) and (-webkit-min-device-pixel-ratio: 1.5) {
  #header {
    display: flex;
    flex-direction: column;
    margin: 20px 0px;
    justify-content: center;
    align-items: center;
  }
}

@media only screen and (min-width: 1366px) and (max-height: 1024px) and (-webkit-min-device-pixel-ratio: 1.5) {
	#header {
	  display: flex;
	  flex-direction: column;
	  margin: 20px 0px;
	  justify-content: center;
	  align-items: center;
	}
  }
  
@media only screen and (min-device-height: 1024px) and (max-device-width: 1366px) and (-webkit-min-device-pixel-ratio: 2) {
  #header {
    display: flex;
    flex-direction: column;
    margin: 20px 0px;
    justify-content: center;
    align-items: center;
  }
}
/* ipad*/
@media only screen and (min-device-width: 1024px) and (max-device-height: 768px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) {
  #header {
    display: flex;
    flex-direction: column;
    margin: 20px 0px;
    justify-content: center;
    align-items: center;
  }
}
@media only screen and (device-width: 768px) and (device-height: 1024px) and (orientation: portrait) {
  #header {
    display: flex;
    flex-direction: column;
    margin: 20px 0px;
    justify-content: center;
    align-items: center;
  }
}
