.hiatusTable {
  grid-template-rows: 30vh 70vh;
}
#row {
  display: flex;
}
#chapterNumber {
  display: none;
}
.Home {
  overflow-y: auto !important;
}
.issue {
  width: 20px;
  height: 20px;
  margin: 0.5px 0.5px;
}
#absent {
  background-color: lightcoral;
}
#active {
  background-color: lightskyblue;
}
#publishingYear {
  display: flex;
}
#year {
  margin-right: 25px;
  width: 15px;
  height: 15px;
  color: white;
}

#table > :last-child > :first-child {
  margin-right: auto;
}

#hiatusTable {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

#hiatusTable > div > h1 {
  color: white;
  text-align: center;
}
 /* ipad */


/* normal phone devices */
@media only screen and (max-width: 825px){
  .issue {
    width: 45px;
    height: 25px;
  }
  #publishingYear {
    flex-wrap: wrap;
	margin: 5px 5px;
  }
  #year {
    margin-right: 0px;
    text-align: center;
    width: auto;
    height: auto;
    color: white;
    font-size: 15px;
  }

  #row {
    display: flex;
    flex-direction: column;
    margin: 0px 25px;
	align-items: center;
  }
  #chapterNumber {
    display: flex;
    margin: 0 0;
    justify-content: center;
    color: white;
    text-shadow: 1px 1px black;
  }

  #hiatusTable > div > h1 {
    margin: 50px 10px;
  }
  #table > :last-child > :first-child {
	margin-right: unset;
  }
}
@media all and (device-width: 768px) and (device-height: 1024px) and (orientation:portrait){
