#homeBody {
  display: flex;
  font-family: couture;
  justify-content: flex-end;
}
#homeBody > div {
  display: flex;
  flex-direction: column;
}

.Homepage {
  grid-template-rows: 45vh 55vh;
}
.home-text {
  color: white;
  font-size: 5.5vmin;
  margin: 0 20px;
  text-align: center;
  word-break: keep-all;
  float: none;
}

#line {
  width: 100%;
  float: left;
  height: 1vh;
  background: white;
  border: none;
}
/* normal phone devices */
@media only screen and (max-width: 767px) and (orientation: portrait){
  #homeBody{
    flex-direction: column;
  }
  #line {
    width: 100%;
    float: left;
    height: 8px;
    background: white;
    border: none;
  }
  .home-text {
    color: white;
    font-size: 6vmin;
    margin: 0 20px;
    text-align: center;
    word-break: keep-all;
    float: none;
  }
  #homeBody > img {
    width: 65vw;
    align-self: center;
    }
}

/* ipad pro*/
@media only screen and (min-device-width: 1024px) and (max-device-height: 1366px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: portrait) {
  #homeBody > img {
    width: 55vw;
    align-self: center;
  }
  #homeBody {
    flex-direction: column;
  }
}
@media only screen and (min-device-width: 481px) and (max-device-width: 824px){
  #homeBody > img {
    width: 25vw;
    align-self: center;
  }
  #homeBody {
    flex-direction: row;
  }
}

/* ipad pro */
@media only screen and (min-device-width: 1366px) and (max-device-height: 1024px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) {
  #homeBody > img {
    width: 40vw;
    align-self: center;
  }
}
/* ipad */

@media all and (device-width: 768px) and (device-height: 1024px) and (orientation:portrait) {
  #homeBody > img {
    width: 40vw;
    align-self: center;
  }
}

@media only screen and (min-device-width: 825px) and (max-device-width: 1023px){
  #homeBody > img {
    width: 40vw;
    align-self: center;
  }
  #homeBody {
    flex-direction: column;
  }
}
